import Image from "next/image";
import DefaultImage from '@/public/images/Default-Image.png'

export default async function PlayerInfoPage({ params }: { params: Promise<{ id: string }> }) {

    const {id} = await params;

    const data = [
        {
            name: "Имя",
            surname: "Фамилия",
            playerNumber: 0,
            leadingLeg: '...',
            schools: 'Школы...',
            firstCoach: 'Тренеры...',
            records: 'Достижения...',
            biography: 'Биография...',
            playersComment: 'Комментарий игрока...',
            src: '',
        }
    ]

    const src = data[0].src !== null && data[0].src !== undefined && data[0].src !== '' ? data[0].src : DefaultImage;

    return(
        <main className="w-full">
            <div className="w-full max-w-[1200px]">
            <Image alt="Фото" src={src}/>
            </div>
        </main>
    )
}